 _c('ul',[_c('li',[_c('h6',{staticClass:"text-left"},[_vm._v("Tanah")]),_c('p',{staticClass:"text-left mb-2",staticStyle:{"text-indent":"15px"}},[_vm._v(" Tanah di Desa Sukolilo memiliki potensi yang sangat baik, pasal nya kondisi lingkungan yang masih alami. Tanah di desa ini dimanfaatkan untuk bertani, salah satu hal yang unik di Desa Sukolilo adalah pertanian rumput. Rumput hasil dari Desa Sukolilo dimanfaatkan menjadi rumput taman / rumput lapangan. Bahkan rumput dari desa ini telah didistribusikan secara nasional ke seluruh wilayah di Indonesia, bahkan juga ada yang sampai di export ke luar negeri. Selain rumput Desa Sukolilo juga memiliki gula merah yang dihasilkan dari percampuran antara tebu hijau dan tebu hitam ini dimasak akhirnya menghasilkan gula merah dengan rasa karamel adanya potensi ini kami akan membuatkan sebuah sistem irigasi secara digital agar petani dapat dipermudah untuk merawat rumput dan tebu. ")])]),_c('li',[_c('h6',{staticClass:"text-left mb-2"},[_vm._v("Air")]),_c('p',{staticClass:"text-left",staticStyle:{"text-indent":"15px"}},[_vm._v(" Air di Desa Sukolilo juga menjadi potensi di daerah ini untuk menambah perekonomian desa. Karena ada Objek wisata yang dikenal sebagai wisata Gentong Mas yang terletak di desa sukolilo. Wisata Gentong Mas memiliki banyak wahana yang bisa menarik minat masyarakat. Disana terdapat kolam renang, danau buatan yang terdapat ikan di dalamnya, saung atau gazebo tempat bersantai wisatawan, disertai pemandangan persawahan yang memanjakan mata. Saat ini, wisata gentong mas masih dalam proses pembangunan. Pembangunannya masih sempat terhambat dikarenakan adanya pandemi sehingga tidak bisa segera dibuka untuk umum. Namun, saat ini wisata gentong mas mulai merencanakan penambahan fasilitas dan mendapat bantuan dari Dinas Sumber Daya Air Tumpang berupa pemberian perahu motor sederhana yang bisa dioperasikan di danau setempat. Fasilitas yang akan dibangun antara lain yaitu outbound air, outbound alam, perahu bebek, kolam sumber, pasar wisata yang menjual souvenir khas desa dan pujasera. ")]),_c('p',{staticClass:"text-left mb-2",staticStyle:{"text-indent":"15px"}},[_vm._v(" Banyak sekali keunggulan yang membuat wisatawan tidak perlu ragu untuk berkunjung ke wisata Gentong Mas. Tidak hanya itu, airnya berasal langsung dari sumber mata air. Sungguh menyegarkan jika kita bisa berenang di air yang segar dengan disuguhkan pemandangan yang masih asri. Wisata gentong mas cocok sekali menjadi pilihan wisata untuk mengusir penat setelah lelah beraktivitas .Adanya objek wisata di tengah-tengah masyarakat desa Sukolilo tentu dapat memberdayakan masyarakat sehingga membantu perekonomian mereka. Untuk membuat wisata gentong mas berkembang sehingga menjadi objek wisata unggulan, perlu adanya peran pemerintah dalam hal pembangunan serta pengenalan kepada khalayak ramai. Dengan adanya potensi seperti ini kami akan membuatkan sistem digital untuk pemasaran wisata dengan bantuan sosial media dan kami juga akan membuatkan sistem digital untuk Wisatawan ketika ingin datang ke wisata Gentong Mas agar wisatawan dan petugas Wisatawan Gentong Mas akan dipermudah untuk menjalankan aktifitas. ")])]),_c('li',[_c('h6',{staticClass:"text-left"},[_vm._v("Manusia")]),_c('p',{staticClass:"text-left mb-2",staticStyle:{"text-indent":"15px"}},[_vm._v(" Kualitas SDM Desa Sukolilo dapat dilihat dari antusias warga dalam bergotong royong dalam membangun desa mereka. Tidak hanya warga kami juga telah melakukan wawancara kepada pihak pemerintahan desa dan mereka sangat mendukung dan siap mengerahkan warga untuk membantu kelancaran proyek kami, seperti contohnya warga desa disini mempertahankan permainan tradisional nyai putut agar tetap ditampilkan dengan pemain warga sekitar Desa Sukolilo dan masih banyak objek pemajuan budaya yang akan dipertahankan warga Desa Sukolilo yang nantinya akan menjadi ciri khas Desa Sukolilo. ")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('ul'