an yang menyebabkan kurang maksimalnya pemanfaatan potensi yang ada berikut contoh masalah dalam pengembangan dan pemanfaatan potensi desa sukolilo ")])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"tab-pane fade",attrs:{"id":"market","role":"tabpanel","aria-labelledby":"market-tab"}},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-lg-6"},[_c('div',{staticClass:"our-services-image mt-50"},[_c('img',{attrs:{"src":"images/our-service-1.jpg","alt":"service"}})])]),_c('div',{staticClass:"col-lg-6"},[_c('div',{staticClass:"our-services-content mt-45"},[_c('h3',{staticClass:"services-title"},[_vm._v(" Solusi "),_c('span',[_vm._v("Yang Kami Tawarkan Untuk Desa Sukolilo.")])]),_c('p',{staticClass:"text"},[_vm._v(" Lorem ipsum dolor sit amet, consectetur adipiscing elit. Nam nec est arcu. Maecenas semper tortor. In elementum in risus sed commodo. Phasellus nisi ligula, luctus at tempor vitae, placerat at ante. Cras sed consequat justo. Curabitur laoreet eu est vel convallis. ")])])])])])}]

