omian warga nya dengan gula merah. Gula merah yang dihasilkan dari percampuran antara tebu hijau dan tebu hitam ini dimasak akhirnya menghasilkan gula merah dengan rasa karamel. Desa sukolilo juga mempunyai tempat wisata gentong mas yang dapat menambah perekonomian desa, dengan Sumber Daya desa yang melimpah sangat disayangkan sekali jika tidak dapat diatur dengan baik oleh karena itu kami akan membuat sistem digital yang dimana semua warga desa maupun pendatang dapat dipermudah untuk menjalankan aktivitasnya. ")]),_c('div',{staticClass:"about-counter pt-60"})])])])])])}]

